export function getWordPool(category: string | string[]): Word[] {
  // N·∫øu l√† array, tr·ªôn c√°c ch·ªß ƒë·ªÅ
  if (Array.isArray(category)) {
    const allWords: Word[] = []
    category.forEach((cat) => {
      if (cat === "mixed") {
        // Tr·ªôn t·∫•t c·∫£ c√°c ch·ªß ƒë·ªÅ
        Object.values(WORD_POOLS).forEach((words) => {
          allWords.push(...words)
        })
      } else {
        const words = WORD_POOLS[cat as keyof typeof WORD_POOLS] || []
        allWords.push(...words)
      }
    })
    return allWords
  }

  // X·ª≠ l√Ω string nh∆∞ tr∆∞·ªõc
  if (category === "mixed") {
    // Tr·ªôn t·∫•t c·∫£ c√°c ch·ªß ƒë·ªÅ
    const allWords: Word[] = []
    Object.values(WORD_POOLS).forEach((words) => {
      allWords.push(...words)
    })
    return allWords
  }
  return WORD_POOLS[category as keyof typeof WORD_POOLS] || WORD_POOLS.family
}

